<template>
  <div>
    <!-- Navbar with "Back" button and centered title -->
    <div class="navbar">
      <button class="btn btn-back" @click="goBack">Back</button>
      <h2 class="title">Add New Character</h2>
      <div class="navbar-spacer"></div>
    </div>

    <!-- Form Container -->
    <div class="form-container">
      <div class="form-group">
        <label for="name">Name:</label>
        <input id="name" v-model="character.name" placeholder="Enter character name" />
      </div>

      <div class="form-group">
        <label for="path">Path:</label>
        <input id="path" v-model="character.path" placeholder="Enter image URL" />
      </div>

      <div class="form-group">
        <label for="htmlCode">HTML Code:</label>
        <textarea id="htmlCode" v-model="character.htmlCode" placeholder="Enter HTML code"></textarea>
      </div>

      <div class="form-group">
        <label for="tags">Tags:</label>
        <input
          id="tags"
          v-model="tagsInput"
          placeholder="Enter tags, separated by commas"
        />
      </div>

      <!-- Action Buttons -->
      <div class="action-buttons">
        <button class="btn btn-save" @click="addCharacter">Add</button>
      </div>

      <!-- Updated Overlay with Juicy Animation -->
      <AppOverlay :visible="added" message="Added!" />
    </div>
  </div>
</template>

<script>
import { push, ref, set } from 'firebase/database'; // Ensure we are using the correct Firebase methods
import { db } from '../firebase'; // Import Firebase DB reference
import AppOverlay from './AppOverlay.vue'; // Use the shared overlay component

export default {
  components: {
    AppOverlay,
  },
  data() {
    return {
      character: {
        name: '',
        path: '',
        htmlCode: '',
        tags: [],
      },
      tagsInput: '',
      added: false,
    };
  },
  methods: {
    async addCharacter() {
      if (!this.character.name || !this.character.path) {
        alert('Name and Path are required.');
        return;
      }

      this.character.tags = this.tagsInput.split(',').map(tag => tag.trim());

      try {
        // Push a new character to the database
        const newCharacterRef = push(ref(db, 'characters')); // Create a new reference in the 'characters' collection
        await set(newCharacterRef, this.character); // Set the character data in Firebase

        // Trigger the overlay animation
        this.added = true;

        // Reset form fields after saving
        this.character.name = '';
        this.character.path = '';
        this.character.htmlCode = '';
        this.tagsInput = '';

        // Hide the overlay after 2 seconds and redirect back to the character list
        setTimeout(() => {
          this.added = false;
          this.$router.push({ name: 'CharacterList' });
        }, 2000);
      } catch (error) {
        console.error('Error adding character:', error);
        alert('Failed to add character.');
      }
    },
    goBack() {
      this.$router.push({ name: 'CharacterList' });
    },
  },
};
</script>

