<template>
  <div class="navbar">
    <!-- Centered title with Add Character button -->
    <h2 class="title">Character Finder v0.3.5</h2>
    <button v-if="showAddButton" class="btn btn-add" @click="navigateToAddCharacter">Add Character</button>
  </div>
</template>

<script>
export default {
  name: 'AppNavbar',
  computed: {
    showAddButton() {
      // Show the Add Character button on pages except the AddCharacter page
      return this.$route.name !== 'AddCharacter';
    }
  },
  methods: {
    navigateToAddCharacter() {
      this.$router.push({ name: 'AddCharacter' });
    }
  }
};
</script>

